*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol,
li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-size: 16px;
  color: #000000;
  font-weight: 700;
  font-family: Inter;
  background: #F5F5F5;
}

._container {
  max-width: 1196px;
  margin: 0 auto;
  padding: 0 15px;
  box-sizing: content-box;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  overflow: hidden;
}

.header {
  width: 100%;
  z-index: 50;
  background-color: #202020;

  &__container {
    padding: 18px 20px;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    font-weight: 500;
    font-size: 18px;
    line-height: 122%;
  }

  &__buttons {
    display: flex;
    align-items: center;
  }

  &__button {
    &--search {
      width: 30px;
      height: 30px;
    }

    &--cart {
      width: 40px;
      height: 40px;
    }
  }

  &__button:not(:last-child) {
    margin-right: 34px;
  }
}

.menu-header {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  &__item {
    padding: 0 30px;
    font-weight: 500;
    font-size: 18px;
    line-height: 122%;
  }
}

@media (max-width: 700px) {
  .menu-header {
    &__item {
      padding: 0 15px;
    }

    &__list {
      padding-left: 25px;
    }
  }
}

@media (max-width: 576px) {
  .menu-header__item {
    padding: 5px 15px;
  }
}

.page {
  flex: 1 1 auto;
}

.title-block {
  text-align: center;

  &__title {
    font-weight: 700;
    font-size: 72px;
    line-height: 120%;
  }
}

._ibg {
  position: relative;
}

._ibg>img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.stats {
  &__body {
    display: flex;
  }

  &__title {
    padding-top: 91px;
    padding-bottom: 28px;
  }

  &__image {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0 1 50%;
    background-image: linear-gradient(234.43deg,
        rgba(210, 214, 49, 0.54) -17.35%,
        rgba(204, 207, 61, 0.808429) 38.06%,
        #D9DD2B 116.67%);
    border-radius: 15px;
    max-width: 600px;
    max-height: 512px;
  }

  &__image img {
    width: 99%;
    height: 99%;
    border-radius: 15px;
  }
}

.happy-clients {
  display: inline-flex;
  flex-direction: column;
  align-items: center;

  &>*:not(:last-child) {
    margin-bottom: 11px;
  }

  &__count {
    font-weight: 700;
    font-size: 24px;
    line-height: 107%;
    letter-spacing: 0.1px;
  }

  &__title {
    font-weight: 400;
    font-size: 16px;
    line-height: 118%;
  }
}

.average-clients {
  display: inline-flex;
  flex-direction: column;
  align-items: center;

  &>*:not(:last-child) {
    margin-bottom: 11px;
  }

  &__count {
    font-weight: 700;
    font-size: 24px;
    line-height: 107%;
    letter-spacing: 0.1px;
  }

  &__title {
    font-weight: 400;
    font-size: 16px;
    line-height: 118%;
  }
}

.client-stats {
  flex: 0 1 50%;
  padding-top: 96px;
  padding-bottom: 247px;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
  }

  &__wrapper>*:not(:last-child) {
    margin-right: 63px;
  }

  &__wrapper>* {
    margin-bottom: 20px;
  }

  &__sub-title {
    font-weight: 400;
    font-size: 16px;
    line-height: 118%;
    max-width: 539px;
  }

  &__sub-title:not(:last-child) {
    margin-bottom: 53px;
  }
}

@media (max-width: 1220px) {
  .stats {
    &__body {
      flex-direction: column;
    }

    &__image {
      align-self: center;
    }

    &__image img {
      width: 500px;
      height: 500px;
    }
  }

  .client-stats {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 0;
    text-align: center;

    &__wrapper {
      justify-content: space-evenly;
    }
  }
}

